.team {
  display: flex;
  justify-content: space-evenly;
  justify-items: center;
  width: 100%;
  gap: 32px 40px;
  padding-bottom: 48px;
  width: fit-content;
  margin: 0 auto;

  @include tablet {
    // display: grid;
  }

  @include under-tablet {
    padding: 0 5%;
    flex-wrap: wrap;
  }

  [data-name='Chris'] {
    order: 1;
  }
  [data-name='Matt'] {
    order: 2;
  }
  [data-name='Steph'] {
    order: 3;
  }

  a {
    width: 100%;
    &:hover {
      figcaption {
        div {
          opacity: 0.8;
        }
      }
    }
  }

  &__member {
    display: flex;
    flex-direction: column;
    gap: 12px;
    max-width: 90%;
    margin: 0 0 36px;
    width: 100%;
    @include tablet {
      max-width: 270px;
      margin: 0 auto 88px;
    }

    img {
      width: 100%;
    }

    figcaption {
      position: relative;
      color: $white;
      p:first-child {
        font-size: 24px;
        font-family: Eina04-Bold;
        text-transform: capitalize;
        @extend .bottom-border;
        border-color: $white;
        padding-bottom: 12px;
        margin-bottom: 16px;
      }

      div {
        position: absolute;
        right: 0;
        top: 0;
        width: 32px;
        height: 32px;
        background: url(../img/linkedin.svg);
        transition: 0.2s;
      }

      p:last-child {
        font-family: Eina04-SemiBold;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.04em;
      }
    }
  }
}
