.logos {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  justify-items: center;
  column-gap: 20px;
  row-gap: 25px;
  width: 100%;
  padding: 0 42px;
  max-width: 1430px;

  @include under-tablet {
    padding: 0 30px;
  }

  .logo {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    width: 100%;

    @include tablet {
      gap: 16px;
    }

    &:hover {
      p {
        color: rgba($color: $gridBlack, $alpha: 1);
      }
    }
  }

  img {
    height: 100%;
    // background: rgba(black, 0.1);
  }

  p {
    text-align: center;
    color: rgba($color: $gridBlack, $alpha: 0.3);
    width: 100%;
  }

  @include under-tablet {
    img {
      width: 100%;
    }
  }

  @include tablet {
    grid-template-columns: repeat(auto-fill, minmax(197px, 1fr));
    row-gap: 90px;
  }

  &__item {
    @include tablet {
      width: 197px;
      @include hoverOpacity();
    }
  }
}

.repeat {
  .logos {
    // width: 100%;
    // padding: 0 5%;
    // perspective: 1000px;

    // display: grid;
    // grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    // justify-items: center;
    // column-gap: 5%;
    // row-gap: 25px;

    // width: 100%;
    // // height: 100%;

    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    justify-items: center;
    column-gap: 20px;
    row-gap: 25px;
    width: 100%;
    padding: 0 42px;
    max-width: 1430px;

    @include under-tablet {
      padding: 0 30px;
    }

    > div {
      transition: transform 0.5s;
      transform-style: preserve-3d;
      cursor: pointer;
      position: relative;

      > a {
        transition: all 0.5s;
      }

      .prior {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        pointer-events: none;
      }
    }

    &.is-flipped {
      > div {
        transform: rotateY(180deg) scaleX(-1);

        .flipped {
          opacity: 0;
        }

        .prior {
          opacity: 1;
          pointer-events: initial;
        }
      }
    }

    .logo {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 12px;
      width: 100%;
      // transition: 0.5s transform;

      @include tablet {
        gap: 16px;
      }

      &:hover {
        p {
          color: rgba($color: $gridBlack, $alpha: 1);
        }
      }
    }

    p {
      text-align: center;
      color: rgba($color: $gridBlack, $alpha: 0.3);
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      width: 100%;
    }

    @include under-tablet {
      img {
        width: 100%;
      }
    }

    @include tablet {
      grid-template-columns: repeat(auto-fill, minmax(197px, 1fr));
      row-gap: 90px;
    }

    &__item {
      @include tablet {
        width: 197px;
        @include hoverOpacity();
      }
    }
  }
}
