*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.filters,
.logos {
  transition: opacity 0.2s;
}

.faded {
  .filters,
  .logos {
    opacity: 0;
  }
}

body {
  font-family: Eina04-Regular;
  color: $white;
}

a {
  color: unset;
  text-decoration: none;
}

ul {
  list-style: none;
}

.flex {
  display: flex;

  &-wrap {
    flex-wrap: wrap;
  }

  &-col {
    flex-direction: column;
  }

  &-aic {
    align-items: center;
  }

  &-jcc {
    justify-content: center;
  }

  &-jsb {
    justify-content: space-between;
  }
}

.sub {
  position: relative;
  width: 100%;
  height: 48px;
  border: 1.87344px solid #ffffff;

  // overflow: hidden;
  @include tablet {
    max-width: 490px;
  }

  input {
    font-family: Eina04-Regular, sans-serif;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
    background: none !important;
    border: none !important;
    width: 70%;
    height: 100%;
    outline: none !important;
    appearance: none !important;
    padding-left: 26px;
    color: $white !important;
  }

  input:-webkit-autofill {
    -webkit-text-fill-color: $white !important;
    color: $white !important;
    transition: background-color 5000s;
  }

  input:autofill {
    color: $white !important;
    -webkit-text-fill-color: $white !important;
    transition: background-color 5000s;
  }

  button {
    // width: 48px;
    height: 48px;
    // border-radius: 100%;
    padding: 0 36px;
    // border: none;
    border: 1px solid $white;
    font-family: Eina04-Regular, sans-serif;
    font-size: 18px;

    cursor: pointer;
    background: $white !important;
    transition: 0.2s background;
    transform: translate(2px, 0px);

    &:hover {
      color: $white !important;
      border-color: $white !important;
      background: transparent !important;
    }
  }

  .email-sent {
    padding: 12.5px;
    width: 100%;
    background: $gridBlack;
    font-family: Eina04-SemiBold, sans-serif;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
  }

  @include under-tablet {
    flex-direction: column;
    height: auto;
    border: none;

    input {
      width: 100%;
      height: 48px;
      border: 1px solid $white !important;
      margin-bottom: 12px;
      padding-left: 16px;
    }

    button {
      width: 100%;
      transform: none;
      color: $gridBlack !important;
      line-height: 19px;
    }
  }
}

.white {
  color: $white;
}

.light-background {
  background: $white;
}

.dark-background {
  background: $gridBlack;
}

.hide-mobile {
  display: none;

  @include tablet {
    display: initial;
  }
}

.uppercase {
  text-transform: uppercase;
}

.hidden {
  display: none !important;
}

.bottom-border {
  border-bottom: 1px solid rgba($color: $gridBlack, $alpha: 0.2);

  &--mobile {
    @include tablet {
      border-bottom: unset;
    }
  }
}
