.filters {
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
  width: 100%;
  max-width: 1450px;
  padding-right: 0px;
  padding-left: 0px;
  @include tablet {
    padding-right: 40px;
    padding-left: 40px;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 70px;
    gap: 50px;
  }
  > div {
    display: flex;

    gap: 12px 16px;
    width: 100%;

    &.type {
      padding: 0 20px;
      overflow-x: scroll;

      @include tablet {
        flex-wrap: wrap;
        padding: 0;
        overflow-x: initial;
      }
    }

    &.logic {
      justify-content: center;
    }

    @include tablet {
      width: unset;
      padding: 0;
      gap: 10px 8px;
      justify-content: flex-start;
    }

    &.type {
      padding-bottom: 20px;
      margin-bottom: 20px;
      @include tablet {
        padding-bottom: 0;
        margin-bottom: 0;
      }
    }

    &.logic {
      flex-wrap: nowrap;
      padding-bottom: 20px;
      margin-bottom: 20px;
      @include tablet {
        padding-bottom: 0;
        margin-bottom: 0;
      }
    }
  }

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    color: $backgroundBlue;
    border: 2px solid transparent;
    background: unset;
    padding: 6px 15px;
    font-family: Eina04-Bold;
    font-size: 14px;
    letter-spacing: 0.04em;
    cursor: pointer;
    transition: 0.2s;
    white-space: nowrap;

    @include tablet {
      font-size: 16px;
      min-width: 114px;
      padding: 10px 13px 8px;
    }

    &.down {
      svg {
        transform: rotate(180deg);
      }
    }

    svg {
      margin-right: 10px;
      transition: 0.2s;
    }

    &:hover {
      border: 2px solid rgba($color: $backgroundBlue, $alpha: 1);
    }

    &.active {
      color: $white;
      background: $backgroundBlue;
      border-color: $backgroundBlue;

      path {
        stroke: $white;
      }
    }
  }
}

