body {
  position: relative;
  width: 100%;
  min-height: 100vh;

  background: radial-gradient(
    67.87% 57.64% at 50% 33.87%,
    $backgroundBlue 0%,
    $gridBlack 100%
  );

  .grid {
    position: absolute;
    width: 100%;
    overflow-x: hidden;
    background-image: linear-gradient($gridBlack 1px, transparent 1px),
      linear-gradient(90deg, $gridBlack 1px, transparent 1px);
    background-size: 46px 47px;
    background-position: center center;
  }
}
