$padding-left: 32px;
$top: 6px;

.header {
  // position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;

  @include tablet {
    // justify-content: space;
    gap: 60px;
  }

  &__logo {
    @include tablet {
      width: 224px;
    }
  }

  &__check {
    display: none;

    &:checked {
      + .header__menu {
        &::after {
          transform: translateX(50px);
        }

        &:hover {
          .header__burger {
            &::after {
              left: 0;
            }
          }
        }

        .header__burger {
          transform: rotate(45deg);
          &::before {
            top: 0;
            opacity: 0;
          }

          &::after {
            top: 0;
            transform: rotate(90deg);
          }
        }
      }

      ~ .header__nav {
        transform: translateX(-5%);
        @include tablet {
          transform: unset;
        }
      }
    }
  }

  &__menu {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 50px;
    cursor: pointer;

    @include tablet {
      display: none;
    }

    // FOND NOIR
    // ! a modif
    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      width: 120vw;
      height: 100vh;
      background: rgba($color: #000000, $alpha: 0.75);
      z-index: 1;
      transform: translateX(-100%);
      transition: 0.5s ease-in-out transform;
    }

    &:hover {
      .header__burger {
        &::before {
          top: $top;
        }

        &::after {
          top: -$top;
        }
      }
    }
  }

  &__burger {
    position: relative;
    width: 20px;
    height: 2px;
    background: $white;
    transition: 0.3s;

    &::after {
      content: "";
      position: absolute;
      top: $top;
      left: 0;
      background: inherit;
      width: inherit;
      height: inherit;
      transition: inherit;
    }

    &::before {
      content: "";
      position: absolute;
      top: -$top;
      left: 0;
      background: inherit;
      width: inherit;
      height: inherit;
      transition: inherit;
    }
  }

  &__nav {
    position: absolute;
    top: 0;
    transform: translateX(100%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    gap: 24px;
    z-index: 100;
    background: $gridBlack;
    width: 100vw;
    height: 100vh;
    padding: 120px 32px 32px;
    transition: 0.5s ease-in-out transform;

    @include tablet {
      position: initial;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      height: 100%;
      transform: unset;
      padding: unset;
      width: 100%;
      background: unset;
    }
  }

  &__close {
    position: absolute;
    top: 26px;
    right: 26px;
    padding: 10px;
    cursor: pointer;

    @include tablet {
      display: none;
    }

    &:hover {
      img {
        transform: rotate(90deg);
      }
    }

    img {
      transition: 0.5s;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 24px;
    width: 100%;

    @include tablet {
      width: 100%;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      gap: 64px;
    }

    li {
      display: flex;
      align-items: center;
      width: 100%;
      @include tablet {
        width: unset;
        height: 61px;
      }
    }

    a {
      display: block;
      font-family: Eina04-Bold;
      font-size: 32px;
      line-height: 38px;
      letter-spacing: 0.04em;
      text-align: center;
      width: 100%;
      color: $white;
      padding: 12px 0;

      @include tablet {
        font-family: inherit;
        font-size: 18px;
        line-height: 21px;
      }

      &:hover {
        text-decoration: underline;
      }

      &.mail {
        @include tablet {
          line-height: 21px;
          text-align: center;
          width: 154px;
          padding: 11px 0;
          border: 1px solid $white;
          color: $white;
          text-align: center;
          text-decoration: none;

          &:hover {
            background-color: $white;
            color: $gridBlack;
          }
        }
      }
    }
  }
}
