.hero {
  // position: relative;
  padding-top: 20px;
  width: 90%;
  margin: 0 auto;
  margin-bottom: 32px;

  @include tablet {
    padding-top: 40px;
    margin: 0 60px 83px 60px;
  }

  &--team {
    margin-bottom: 0;

    @include tablet {
      margin-bottom: 60px;
      align-items: center;
    }

    @include under-tablet {
      .title {
        margin-bottom: 32px !important;
        margin-top: 42px !important;
      }
    }
  }

  .title {
    font-family: Eina04-Bold, sans-serif;
    font-size: 32px;
    line-height: 1.2;
    letter-spacing: 0.04em;
    max-width: 888px;
    margin-bottom: 32px;
    margin-top: 42px;

    @include tablet {
      font-family: Eina04-Bold, sans-serif;
      font-size: 36px;
      text-align: center;
      margin-bottom: 42px;
      margin-top: 72px;
    }
  }

  p {
    max-width: 1068px;
    font-size: 21px;
    line-height: 25px;
    letter-spacing: 0.04em;
    text-align: center;
  }

  .result {
    margin-top: 16px;
    font-family: Eina04-Regular, sans-serif;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: center;
  }
}
