.powered {
  margin-top: 64px;
  color: rgba($color: $gridBlack, $alpha: 0.2);
  padding: 0 5%;

  @include tablet {
    margin-top: 102px;
  }

  h2 {
    font-family: Eina04-Bold, sans-serif;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: 0.04em;
    margin-bottom: 19px;

    @include tablet {
      font-size: 16px;
      margin-bottom: 7px;
    }
  }

  img {
    @include tablet {
      width: 162px;
    }
  }
}
